.header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.header .header--imgcontainer {
    width: 100%;
    max-width: 180px; 
    margin: 100px 0 0px 0;
}

.header img {
    width: 100%;
    transition: all .2s;
    -webkit-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -o-transition: all .2s ease;
}

.header img:hover {
    transform: scale(1.2);
}