.Home {
    font-family: Arial;
    background: #fffff4;
    min-height: 100vh;
    width: 100%;
}

.Home .home--corpo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
    width: 100%;
}

.Home .home--corpo h2 {
    line-height: 100%;
    margin: 5px 0;
    padding: 0;
    font-weight: 500;
    font-size: 20x;
}

.Home .home--socialbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 70px;
}

.Home .home--socialbar a {
    color: #000;
    font-size: 20px;
    margin: 0 10px;
    transition: all .2s;
    -webkit-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -o-transition: all .2s ease;
}

.Home .home--socialbar a:hover {
    font-size: 30px;
}

.Home .home--socialbar a:visited {
    color: #000;
}

.Home .home--imagens-biomas--container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.Home .home--imagens-biomas--container--coluna1,
.Home .home--imagens-biomas--container--coluna2 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    width: 35%;
    margin: 10px;
    gap: 20px;
}

.Home .home--imagens-biomas--container--imgcontainer {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Home .home--imagens-biomas--container img {
    width: 100%;
    transition: all .2s;
    -webkit-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -o-transition: all .2s ease;
}

.Home .home--imagens-biomas--container img:hover {
    transform: scale(1.01);
}

@media all and (max-width: 760px)
and (orientation: portrait) {
    .Home .home--imagens-biomas--container {
        flex-direction: column;
    }
    .Home .home--imagens-biomas--container--coluna1,
    .Home .home--imagens-biomas--container--coluna2 {
        width: 90%;
    }
}

