.footer-container {
    width: 100%;
    color: #8b8b8b;
    min-height: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    margin: 50px 0 10px 0;
}

.footer-container .footer {
    width: 90%;
    text-align: left;
}

.footer-container .footer .divisor {
    height: 1px;
    width: 100%;
    margin: 5px 0;
    background-color: #eee;
}

.footer-container .footer img {
    width: 100%;
    max-width: 60px;
    opacity: 0.43;
    transition: all .2s;
    -webkit-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -o-transition: all .2s ease;
}

.footer-container .footer img:hover {
    opacity: 1;
}


@media all and (max-width: 760px)
and (orientation: portrait) {
    .footer-container {
        margin: 50px 0;
    }
    .footer-container .footer {
        text-align: center;
    }
    .footer-container .footer img {
        width: 100%;
        max-width: 50%;
    }
    .footer-container .footer .divisor {
        margin: 15px 0;
    }
}
